.container {
  height: 100vh;
  width: 100vw;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bkg {
  height: 100vh;
  width: 100vw;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
  position: absolute;
  z-index: 1;
}

.glass {
  width: 1000px;
  height: 800px;
  position: absolute;
  z-index: 2;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  overflow: hidden;
}

.glass > div {
  width: 100%;
}

.title {
  font-family: "Matemasie";
  font-size: 48px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(94, 95, 95, 0.329);
  text-shadow: white 1px 0 10px;
}

.game {
  height: 550px;
  display: flex;
}
.footer {
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 32px;
  color: white;
}
